/**
 * API
 * API
 */
class RemoteAPIModelService {
  /**
   * 
   */
  constructor() {
    this.model = null;
    this.isLoading = false;
    this.progress = 0;
    this.error = null;
    this.apiEndpoint = 'https://api.jsppt.com/generate'; // API
  }

  /**
   * 
   * @returns {Boolean} 
   */
  isModelLoaded() {
    return this.model !== null;
  }

  /**
   * 
   * @returns {Number} 0-1
   */
  getProgress() {
    return this.progress;
  }

  /**
   * 
   * @param {Object} callbacks 
   * @returns {Promise<Boolean>} 
   */
  async loadModel(callbacks = {}) {
    if (this.isLoading) return false;

    try {
      this.isLoading = true;
      this.error = null;
      
      // 
      this.progress = 0;
      if (callbacks.onProgress) {
        callbacks.onProgress(this.progress);
      }

      // API
      for (let i = 0; i < 3; i++) {
        await new Promise(resolve => setTimeout(resolve, 500));
        this.progress = (i + 1) / 3;
        if (callbacks.onProgress) {
          callbacks.onProgress(this.progress);
        }
      }

      // API
      this.model = {
        generate: async (prompt) => {
          // API
          await new Promise(resolve => setTimeout(resolve, 2000));
          return { text: this.generateDummyMarkdown(prompt) };
        }
      };

      if (callbacks.onSuccess) {
        callbacks.onSuccess();
      }

      return true;
    } catch (error) {
      this.error = error;
      console.error("API:", error);

      if (callbacks.onError) {
        callbacks.onError(error);
      }

      return false;
    } finally {
      this.isLoading = false;
    }
  }

  /**
   * MarpMarkdown
   * @param {String} prompt 
   * @returns {Promise<String>} Markdown
   */
  async generateMarpMarkdown(prompt) {
    if (!this.isModelLoaded()) {
      throw new Error("API");
    }

    try {
      // API
      const response = await this.model.generate(prompt);
      return response.text;
    } catch (error) {
      console.error(":", error);
      throw error;
    }
  }

  /**
   * 
   * @returns {Promise<void>}
   */
  async unloadModel() {
    if (this.model) {
      try {
        // 
        await new Promise(resolve => setTimeout(resolve, 200));
        this.model = null;
      } catch (error) {
        console.error(":", error);
      }
    }
  }

  /**
   * Markdown
   * @param {String} prompt 
   * @returns {String} Markdown
   */
  generateDummyMarkdown(prompt) {
    return `---
marp: true
theme: default
paginate: true
---

# ${prompt}

---

## 

1. 
2. 
3. 
4. 

---

## 

- ${prompt}
- 
- 

---

## 

- 
- 
- 
- 

---

## 

- 1
- 2
- 3

---

## 

- ${prompt}
- 
- 

---

# 

example@jsppt.com`;
  }
}

export default RemoteAPIModelService;
