/**
 * 
 * WebGPUWebGL
 */
class CompatibilityService {
  /**
   * WebGPU
   * @returns {Promise<Object>} 
   */
  async checkWebGPUSupport() {
    if (!navigator.gpu) {
      return {
        supported: false,
        reason: 'WebGPU API'
      };
    }

    try {
      // 
      const adapter = await navigator.gpu.requestAdapter();
      if (!adapter) {
        return {
          supported: false,
          reason: 'WebGPU'
        };
      }

      // 
      const device = await adapter.requestDevice();
      if (!device) {
        return {
          supported: false,
          reason: 'WebGPU'
        };
      }

      return {
        supported: true,
        adapter: adapter,
        device: device
      };
    } catch (error) {
      return {
        supported: false,
        reason: `WebGPU: ${error.message}`
      };
    }
  }

  /**
   * WebGL
   * @returns {Object} 
   */
  checkWebGLSupport() {
    try {
      const canvas = document.createElement('canvas');
      const gl = canvas.getContext('webgl2') || canvas.getContext('webgl');

      if (!gl) {
        return {
          supported: false,
          reason: 'WebGL'
        };
      }

      return {
        supported: true,
        version: gl instanceof WebGL2RenderingContext ? 2 : 1,
        context: gl
      };
    } catch (error) {
      return {
        supported: false,
        reason: `WebGL: ${error.message}`
      };
    }
  }

  /**
   * 
   * @returns {Object} 
   */
  checkDevicePerformance() {
    const memory = navigator.deviceMemory || 4; // 4GB
    const cores = navigator.hardwareConcurrency || 4; // 4

    // 
    const performanceScore = (memory * cores) / 4;

    return {
      memory,
      cores,
      performanceScore,
      // 
      performanceTier: performanceScore >= 8 ? 'high' : (performanceScore >= 4 ? 'medium' : 'low')
    };
  }

  /**
   * 
   * @returns {Object} 
   */
  getBrowserInfo() {
    const userAgent = navigator.userAgent;
    let browserName = "";
    let browserVersion = "";

    // Chrome
    if (userAgent.indexOf("Chrome") > -1 && userAgent.indexOf("Edg") === -1) {
      browserName = "Chrome";
      browserVersion = userAgent.match(/Chrome\/([0-9.]+)/)[1];
    }
    // Edge
    else if (userAgent.indexOf("Edg") > -1) {
      browserName = "Edge";
      browserVersion = userAgent.match(/Edg\/([0-9.]+)/)[1];
    }
    // Firefox
    else if (userAgent.indexOf("Firefox") > -1) {
      browserName = "Firefox";
      browserVersion = userAgent.match(/Firefox\/([0-9.]+)/)[1];
    }
    // Safari
    else if (userAgent.indexOf("Safari") > -1) {
      browserName = "Safari";
      browserVersion = userAgent.match(/Safari\/([0-9.]+)/)[1];
    }

    return {
      name: browserName,
      version: browserVersion
    };
  }

  /**
   * 
   * @returns {Boolean} 
   */
  isMobileDevice() {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  }

  /**
   * 
   * @returns {Promise<Object>} 
   */
  async getFullCompatibilityReport() {
    const webgpuSupport = await this.checkWebGPUSupport();
    const webglSupport = this.checkWebGLSupport();
    const performance = this.checkDevicePerformance();
    const browserInfo = this.getBrowserInfo();
    const isMobile = this.isMobileDevice();

    // 
    let recommendedStrategy = "API";
    let expectedPerformance = "";

    if (webgpuSupport.supported) {
      recommendedStrategy = "WebGPU";
      expectedPerformance = "";
    } else if (webglSupport.supported) {
      recommendedStrategy = "WebGL";
      expectedPerformance = "";
    } else if (performance.performanceTier !== "low") {
      recommendedStrategy = "WASM CPU";
      expectedPerformance = "";
    }

    return {
      browser: browserInfo,
      isMobile,
      webgpu: webgpuSupport,
      webgl: webglSupport,
      performance,
      recommendedStrategy,
      expectedPerformance
    };
  }
}

// 
const compatibilityService = new CompatibilityService();
export default compatibilityService;
