/**
 * MarkdownMarp
 * @param {String} markdown Markdown
 * @param {String} theme 
 * @returns {String} MarpMarkdown
 */
function ensureMarpMetadata(markdown, theme = 'default') {
  // 
  if (markdown.startsWith('---\n')) {
    // marp: true
    if (!markdown.includes('marp: true')) {
      // marp: true
      markdown = markdown.replace('---\n', '---\nmarp: true\n');
    }

    // theme
    if (!markdown.includes('theme:')) {
      // theme
      markdown = markdown.replace('---\n', `---\ntheme: ${theme}\n`);
    } else {
      // theme
      markdown = markdown.replace(/theme:.*\n/, `theme: ${theme}\n`);
    }

    // paginate
    if (!markdown.includes('paginate:')) {
      // paginate
      markdown = markdown.replace('---\n', '---\npaginate: true\n');
    }

    return markdown;
  } else {
    // 
    return `---
marp: true
theme: ${theme}
paginate: true
---

${markdown}`;
  }
}

/**
 * MarkdownHTML
 * @param {String} markdown Markdown
 * @param {String} theme 
 * @returns {Promise<Object>} HTMLCSS
 */
async function renderMarkdownToHTML(markdown, theme = 'default') {
  try {
    // Marp
    const { Marp } = await import('@marp-team/marp-core');

    // Marp
    const marp = new Marp({
      inlineSVG: true,
      html: true, // HTML
      math: true, // 
      minifyCSS: false // CSS
    });

    // 
    Object.entries(CUSTOM_THEMES).forEach(([themeId, css]) => {
      marp.themeSet.add(css);
    });

    // Markdown
    const processedMarkdown = ensureMarpMetadata(markdown, theme);

    // Markdown
    const { html, css, comments } = marp.render(processedMarkdown);

    // CSSHTML
    const fullHTML = `
<!DOCTYPE html>
<html lang="zh-CN">
<head>
  <meta charset="UTF-8">
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
  <title>jsPPT</title>
  <style>
    /*  */
    body {
      margin: 0;
      padding: 0;
      background-color: #f0f0f0;
    }
    .marp-container {
      width: 100%;
      height: 100%;
      overflow: auto;
    }
    /* Marp */
    ${css}
    /*  */
    @media print {
      body {
        background-color: white;
      }
      .marp-container {
        width: 100%;
        height: auto;
      }
    }
  </style>
  <script>
    // 
    document.addEventListener('DOMContentLoaded', () => {
      const sections = document.querySelectorAll('section');
      let currentSlide = 0;

      function goToSlide(index) {
        if (index >= 0 && index < sections.length) {
          sections[currentSlide].style.display = 'none';
          currentSlide = index;
          sections[currentSlide].style.display = 'flex';
        }
      }

      // 
      sections.forEach((section, index) => {
        section.style.display = index === 0 ? 'flex' : 'none';
      });

      // 
      document.addEventListener('keydown', (e) => {
        if (e.key === 'ArrowRight' || e.key === 'PageDown' || e.key === ' ') {
          goToSlide(currentSlide + 1);
        } else if (e.key === 'ArrowLeft' || e.key === 'PageUp') {
          goToSlide(currentSlide - 1);
        } else if (e.key === 'Home') {
          goToSlide(0);
        } else if (e.key === 'End') {
          goToSlide(sections.length - 1);
        }
      });

      // 
      let touchStartX = 0;
      document.addEventListener('touchstart', (e) => {
        touchStartX = e.touches[0].clientX;
      });
      document.addEventListener('touchend', (e) => {
        const touchEndX = e.changedTouches[0].clientX;
        const diff = touchStartX - touchEndX;

        if (Math.abs(diff) > 50) { // 
          if (diff > 0) {
            // 
            goToSlide(currentSlide + 1);
          } else {
            // 
            goToSlide(currentSlide - 1);
          }
        }
      });
    });
  </script>
</head>
<body>
  <div class="marp-container">
    ${html}
  </div>
</body>
</html>
    `;

    return {
      html: fullHTML,
      rawHtml: html,
      css,
      comments,
      slideCount: (html.match(/<section/g) || []).length
    };
  } catch (error) {
    console.error('Markdown:', error);
    throw error;
  }
}

/**
 * PDF
 * @param {String} markdown Markdown
 * @param {String} filename 
 * @param {String} theme 
 * @param {Object} options 
 * @returns {Promise<Boolean>} 
 */
/**
 * PDF
 *
 * jsPDF
 * "Cannot access uninitialized variable"
 *
 * @param {String} markdown Markdown
 * @param {String} filename 
 * @param {String} theme 
 * @param {Object} options 
 * @returns {Promise<Boolean>} 
 */
export async function exportToPDF(markdown, filename = 'presentation.pdf', theme = 'default', options = {}) {
  try {
    // MarkdownHTML
    const renderResult = await renderMarkdownToHTML(markdown, theme);
    const { html: fullHTML, slideCount } = renderResult;

    // 
    const printWindow = window.open('', '_blank');
    if (!printWindow) {
      throw new Error('');
    }

    // 
    const tempDiv = document.createElement('div');
    tempDiv.innerHTML = fullHTML;
    const styleContent = tempDiv.querySelector('style')?.innerHTML || '';

    // HTMLVueHTML
    const doctype = '<!DOCTYPE html>';
    const htmlOpen = '<html>';
    const htmlClose = '</html>';
    const headOpen = '<head>';
    const headClose = '</head>';
    const bodyOpen = '<body>';
    const bodyClose = '</body>';
    const title = '<title>jsPPT</title>';
    const styleOpen = '<style>';
    const styleClose = '</style>';
    const divOpen = '<div class="print-container">';
    const divClose = '</div>';

    // 
    const cssContent = `
      body { margin: 0; padding: 0; }
      .print-container { width: 100%; }
      section {
        page-break-after: always;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 40px;
        box-sizing: border-box;
      }
      section:last-child { page-break-after: auto; }
      @media print {
        body { background: white; }
        section {
          height: 100%;
          page-break-inside: avoid;
        }
      }
      ${styleContent}
    `;

    // 
    const scriptOpen = '<scr' + 'ipt>';
    const scriptClose = '</scr' + 'ipt>';
    const scriptBody = `
      // 
      window.onload = function() {
        setTimeout(function() {
          window.print();
        }, 500);
      };
    `;
    const scriptContent = scriptOpen + scriptBody + scriptClose;

    // HTML
    const htmlContent =
      doctype +
      htmlOpen +
      headOpen +
      title +
      styleOpen +
      cssContent +
      styleClose +
      headClose +
      bodyOpen +
      divOpen +
      tempDiv.innerHTML +
      divClose +
      scriptContent +
      bodyClose +
      htmlClose;

    // HTML
    printWindow.document.write(htmlContent);
    printWindow.document.close();

    console.log(`PDF${slideCount}`);
    return true;
  } catch (error) {
    console.error('PDF:', error);
    throw error;
  }
}

