/**
 * 
 * IndexedDB
 */
class CacheService {
  /**
   * 
   */
  constructor() {
    this.dbName = 'jsPPT-ModelCache';
    this.storeName = 'models';
    this.db = null;
  }

  /**
   * 
   * @returns {Promise<IDBDatabase>} IndexedDB
   */
  async initDB() {
    if (this.db) return this.db;

    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName, 1);

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains(this.storeName)) {
          db.createObjectStore(this.storeName);
        }
      };

      request.onsuccess = (event) => {
        this.db = event.target.result;
        resolve(this.db);
      };

      request.onerror = (event) => {
        console.error('IndexedDB:', event.target.error);
        reject(event.target.error);
      };
    });
  }

  /**
   * 
   * @param {String} modelId ID
   * @returns {Promise<Boolean>} 
   */
  async hasModel(modelId) {
    try {
      await this.initDB();

      return new Promise((resolve) => {
        const transaction = this.db.transaction(this.storeName, 'readonly');
        const store = transaction.objectStore(this.storeName);
        const request = store.get(modelId);

        request.onsuccess = () => {
          resolve(!!request.result);
        };

        request.onerror = () => {
          console.error(':', request.error);
          resolve(false);
        };
      });
    } catch (error) {
      console.error(':', error);
      return false;
    }
  }

  /**
   * 
   * @param {String} modelId ID
   * @param {*} modelData 
   * @returns {Promise<Boolean>} 
   */
  async saveModel(modelId, modelData) {
    try {
      await this.initDB();

      return new Promise((resolve, reject) => {
        const transaction = this.db.transaction(this.storeName, 'readwrite');
        const store = transaction.objectStore(this.storeName);
        const request = store.put(modelData, modelId);

        request.onsuccess = () => {
          resolve(true);
        };

        request.onerror = (event) => {
          console.error(':', event.target.error);
          reject(event.target.error);
        };
      });
    } catch (error) {
      console.error(':', error);
      return false;
    }
  }

  /**
   * 
   * @param {String} modelId ID
   * @returns {Promise<*>} 
   */
  async getModel(modelId) {
    try {
      await this.initDB();

      return new Promise((resolve, reject) => {
        const transaction = this.db.transaction(this.storeName, 'readonly');
        const store = transaction.objectStore(this.storeName);
        const request = store.get(modelId);

        request.onsuccess = () => {
          resolve(request.result);
        };

        request.onerror = (event) => {
          console.error(':', event.target.error);
          reject(event.target.error);
        };
      });
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  }

  /**
   * 
   * @param {String} modelId ID
   * @returns {Promise<Boolean>} 
   */
  async deleteModel(modelId) {
    try {
      await this.initDB();

      return new Promise((resolve, reject) => {
        const transaction = this.db.transaction(this.storeName, 'readwrite');
        const store = transaction.objectStore(this.storeName);
        const request = store.delete(modelId);

        request.onsuccess = () => {
          resolve(true);
        };

        request.onerror = (event) => {
          console.error(':', event.target.error);
          reject(event.target.error);
        };
      });
    } catch (error) {
      console.error(':', error);
      return false;
    }
  }

  /**
   * 
   * @returns {Promise<Boolean>} 
   */
  async clearCache() {
    try {
      await this.initDB();

      return new Promise((resolve, reject) => {
        const transaction = this.db.transaction(this.storeName, 'readwrite');
        const store = transaction.objectStore(this.storeName);
        const request = store.clear();

        request.onsuccess = () => {
          resolve(true);
        };

        request.onerror = (event) => {
          console.error(':', event.target.error);
          reject(event.target.error);
        };
      });
    } catch (error) {
      console.error(':', error);
      return false;
    }
  }
}

// 
const cacheService = new CacheService();
export default cacheService;
