/**
 * WebGL
 * WebGL
 */
import cacheService from './cacheService';

class WebGLModelService {
  /**
   * 
   * @param {WebGLRenderingContext|WebGL2RenderingContext} context WebGL
   * @param {Number} version WebGL
   */
  constructor(context, version) {
    this.context = context;
    this.version = version;
    this.model = null;
    this.isLoading = false;
    this.progress = 0;
    this.error = null;
    this.modelId = 'qwen3-0.6b';
    this.modelUrl = 'https://cdn.jsppt.com/models/qwen3-0.6b-q4.gguf';
  }

  /**
   * 
   * @returns {Boolean} 
   */
  isModelLoaded() {
    return this.model !== null;
  }

  /**
   * 
   * @returns {Number} 0-1
   */
  getProgress() {
    return this.progress;
  }

  /**
   * 
   * @param {Object} callbacks 
   * @returns {Promise<Boolean>} 
   */
  async loadModel(callbacks = {}) {
    if (this.isLoading) return false;

    try {
      this.isLoading = true;
      this.error = null;

      // 
      const isCached = await cacheService.hasModel(this.modelId);
      
      // 
      this.progress = 0;
      if (callbacks.onProgress) {
        callbacks.onProgress(this.progress);
      }

      // 
      // WebLLM
      if (!isCached) {
        // 
        for (let i = 0; i < 10; i++) {
          await new Promise(resolve => setTimeout(resolve, 600)); // WebGLWebGPU
          this.progress = (i + 1) / 10;
          if (callbacks.onProgress) {
            callbacks.onProgress(this.progress);
          }
        }
        
        // 
        await cacheService.saveModel(this.modelId, { timestamp: Date.now() });
      } else {
        // 
        for (let i = 0; i < 4; i++) {
          await new Promise(resolve => setTimeout(resolve, 400));
          this.progress = (i + 1) / 4;
          if (callbacks.onProgress) {
            callbacks.onProgress(this.progress);
          }
        }
      }

      // 
      this.model = {
        generate: async (prompt) => {
          // 
          await new Promise(resolve => setTimeout(resolve, 3000)); // WebGLWebGPU
          return { text: this.generateDummyMarkdown(prompt) };
        }
      };

      if (callbacks.onSuccess) {
        callbacks.onSuccess();
      }

      return true;
    } catch (error) {
      this.error = error;
      console.error(":", error);

      if (callbacks.onError) {
        callbacks.onError(error);
      }

      return false;
    } finally {
      this.isLoading = false;
    }
  }

  /**
   * MarpMarkdown
   * @param {String} prompt 
   * @returns {Promise<String>} Markdown
   */
  async generateMarpMarkdown(prompt) {
    if (!this.isModelLoaded()) {
      throw new Error("");
    }

    try {
      // 
      const response = await this.model.generate(prompt);
      return response.text;
    } catch (error) {
      console.error(":", error);
      throw error;
    }
  }

  /**
   * 
   * @returns {Promise<void>}
   */
  async unloadModel() {
    if (this.model) {
      try {
        // 
        await new Promise(resolve => setTimeout(resolve, 500));
        this.model = null;
      } catch (error) {
        console.error(":", error);
      }
    }
  }

  /**
   * Markdown
   * @param {String} prompt 
   * @returns {String} Markdown
   */
  generateDummyMarkdown(prompt) {
    return `---
marp: true
theme: default
paginate: true
---

# ${prompt}

---

## 

1. 
2. 
3. 
4. 

---

## 

- ${prompt}
- 
- 

---

## 

- 
- 
- 
- 

---

## 

- 1
- 2
- 3

---

## 

- ${prompt}
- 
- 

---

# 

example@jsppt.com`;
  }
}

export default WebGLModelService;
