            <div class="setting-group">
              <label for="default-theme">:</label>
              <select id="default-theme" v-model="selectedTheme" class="select-input">
                <option v-for="theme in availableThemes" :key="theme.id" :value="theme.id">
                  {{ theme.name }}
                </option>
              </select>
            </div>
          </div>

          <div class="settings-section">
            <h3></h3>
            <p>jsPPT : 1.0.0</p>
            <p> Vue.js  Marp </p>
            <p>: Qwen 3.0-0.6B</p>
          </div>
        </div>
        <div class="modal-footer">
          <button @click="showSettings = false" class="btn btn-primary"></button>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-if="showHelp" class="modal-overlay" @click.self="showHelp = false">
      <div class="modal-container">
        <div class="modal-header">
          <h2></h2>
          <button class="close-btn" @click="showHelp = false"></button>
        </div>
        <div class="modal-content">
          <div class="help-section">
            <h3></h3>
            <ol class="help-steps">
              <li></li>
              <li>""</li>
              <li>AI</li>
              <li>""</li>
              <li>"PDF""PPTX"</li>
            </ol>
          </div>

          <div class="help-section">
            <h3></h3>
            <ul class="help-tips">
              <li></li>
              <li>""</li>
              <li>""</li>
              <li>Markdown</li>
            </ul>
          </div>

          <div class="help-section">
            <h3></h3>
            <div class="shortcut-list">
              <div class="shortcut-item">
                <span class="shortcut-key">Ctrl+Enter</span>
                <span class="shortcut-desc"></span>
              </div>
              <div class="shortcut-item">
                <span class="shortcut-key">Esc</span>
                <span class="shortcut-desc"></span>
              </div>
              <div class="shortcut-item">
                <span class="shortcut-key">/</span>
                <span class="shortcut-desc"></span>
              </div>
            </div>
          </div>
